        [preferences, setPreferences] = useState(initialPreferences),
        [firstStep, setFirstStep] = useState(false), // Controls 'Start again' visibility
        [windowPrompt, setWindowPrompt] = useState(
            'Choose the qualification you wish to apply for:'
        );
    let [selection, setSelection] = useState();
    let [btnSelection, setBtnSelection] = useState();
    let [linkSelection, setLinkSelection] = useState();
    // let [initialSelection, setInitialSelection] = useState();
    // let [qualificationSelect, setQualificationSelect] = useState(false);
    let options = document.querySelector('.how-to-apply--pgt--js__options'),
        // promptQualification = 'Choose the qualification you wish to apply for:',
        promptRoute = 'Choose the route you wish to apply for:';
    // promptEntryPoint = 'Choose the entry point you wish to apply for:';

    function filterMethodsData(methods) {
        // console.log(methods);
        // console.log(methods.length);
        // console.log('filter methods function');
        // No additional validation required at this level; print methods of study as links
        options = (
            <ul className="how-to-apply--pgt--js__options">
                <ApplyLinks data={methods} />
            </ul>
        );
        setSelection(options);
        setBtnSelection();
        setLinkSelection();
    }

    function filterDatesData(filterDatesData) {
        // console.log('fdd: ' + filterDatesData);

        let methodStandardButtons = [];
        let methodLinkButtons = [];
        // Loop dates; see if they can be direct links
        filterDatesData.map((d) => {
            let locations = d['options'];
            locations.map((l) => {
                // console.log(l);
                let methods = l['options'];
                methods.map(() => {
                    // console.log('dates: ' + filterDatesData.length);
                    // console.log('locations: ' + locations.length);
                    // console.log('methods: ' + methods.length);
                    // console.log(filterDatesData.length);

                    if (filterDatesData.length > 1) {
                        if (locations.length > 1 || methods.length > 1) {
                            methodStandardButtons.push(d.header);
                        } else {
                            methodLinkButtons.push(d.header);
                        }
                    } else {
                        // Single date; skip location level as handled in dates window and move to methods
                        filterMethodsData(
                            filterDatesData[0]['options'][0]['options']
                        );
                    }
                });
            });
        });
    }

    function filterSubjectData(data) {
        setBtnSelection();
        // console.log('fsdf: ' + data);
        data.map((s) => {
            // console.log(s);
            let subjectNames = s['options'];
            subjectNames.map((s) => {
                let dates = s['options'];
                dates.map((d) => {
                    // console.log(d);
                    let locations = d['options'];
                    locations.map((l) => {
                        let methods = l['options'];
                        if (
                            subjectNames.length > 1 &&
                            (dates.length ||
                                locations.length ||
                                methods.length > 1)
                        ) {
                            // Multiple dates, locations and/or methods exist. Render Regular buttons. Next window content decided in filterDatesData function.
                            options = (
                                <ul className="how-to-apply--pgt--js__options">
                                    {subjectNames.map((s) => {
                                        return (
                                            <li key={s}>
                                                <button
                                                    onClick={(e) => {
                                                        let subjBtn = e.target.querySelector(
                                                            'span'
                                                        );
                                                        let subjVal =
                                                            subjBtn.textContent;
                                                        let filteredSubjData = subjectNames.filter(
                                                            (s) =>
                                                                s.header ===
                                                                subjVal
                                                        );
                                                        filterDatesData(
                                                            filteredSubjData
                                                        );
                                                    }}
                                                >
                                                    <span
                                                        onClick={(e) => {
                                                            let spanText =
                                                                e.target
                                                                    .textContent;
                                                            let filteredSubjData = subjectNames.filter(
                                                                (s) =>
                                                                    s.header ===
                                                                    spanText
                                                            );
                                                            filterDatesData(
                                                                filteredSubjData
                                                            );
                                                        }}
                                                    >
                                                        {s.header}
                                                    </span>
                                                </button>
                                            </li>
                                        );
                                    })}
                                </ul>
                            );
                            setWindowPrompt(promptRoute);
                            setSelection(options);
                        } else if (
                            subjectNames.length > 1 &&
                            (dates.length ||
                                locations.length ||
                                methods.length === 1)
                        ) {
                            // Display subjects as links
                        } else if (
                            subjectNames.length === 1 &&
                            (dates.length ||
                                locations.length ||
                                methods.length > 1)
                        ) {
                            // Open next window (dates)
                            // console.log('open dates window');
                            filterDatesData(subjectNames[0]['options']);
                        }
                    });
                });
            });
        });
    }

    function filterQualificationData(data) {
        let buttonQuals = [];
        let linkQuals = [];
        let buttonOptions;
        let linkOptions;
        data.length > 1
            ? data.map((e) => {
                  let subjectNames = e['options'];
                  // console.log(subjectNames);
                  // console.log(subjectNames.length);
                  subjectNames.map((s) => {
                      // console.log('TL subjects: ' + s);
                      // console.log('TL subjects: ' + s.length);
                      let dates = s['options'];
                      dates.map((d) => {
                          // console.log('TL dates: ' + d.length);
                          let locations = d['options'];
                          locations.map((l) => {
                              // console.log('TL locations: ' + l.length);
                              let methods = l['options'];
                              methods.map(() => {
                                  // console.log('TL methods: ' + m.length);
                                  if (data.length > 1) {
                                      if (
                                          subjectNames.length > 1 ||
                                          dates.length > 1 ||
                                          locations.length > 1 ||
                                          methods.length > 1
                                      ) {
                                          buttonQuals.push(e.header);
                                          buttonQuals = buttonQuals.reduce(
                                              function (a, i) {
                                                  a.indexOf(i) === -1
                                                      ? a.push(i)
                                                      : null;
                                                  return a;
                                              },
                                              []
                                          );
                                          buttonOptions = buttonQuals.map(
                                              (bq) => {
                                                  return (
                                                      <li key={bq}>
                                                          <button
                                                              onClick={(e) => {
                                                                  let qualBtn = e.target.querySelector(
                                                                      'span'
                                                                  );
                                                                  // console.log(qualBtn);
                                                                  let qualVal =
                                                                      qualBtn.textContent;
                                                                  // console.log(qualVal);
                                                                  let filteredQualData = data.filter(
                                                                      (s) =>
                                                                          s.header ===
                                                                          qualVal
                                                                  );
                                                                  // console.log(filteredQualData);
                                                                  filteredQualData;
                                                                  filterSubjectData(
                                                                      filteredQualData
                                                                  );
                                                              }}
                                                          >
                                                              <span
                                                                  onClick={() => {
                                                                      // console.log('span btn click');
                                                                  }}
                                                              >
                                                                  {bq}
                                                              </span>
                                                          </button>
                                                      </li>
                                                  );
                                              }
                                          );
                                          setBtnSelection(buttonOptions);
                                      } else {
                                          linkQuals.push(e.header);
                                          linkQuals = linkQuals.reduce(
                                              function (a, i) {
                                                  a.indexOf(i) === -1
                                                      ? a.push(i)
                                                      : null;
                                                  return a;
                                              },
                                              []
                                          );
                                          linkOptions = linkQuals.map((lq) => {
                                              return (
                                                  <ApplyLinks
                                                      key={lq}
                                                      data={methods}
                                                      btnText={lq}
                                                  />
                                              );
                                          });
                                          setLinkSelection(linkOptions);
                                      }
                                  }
                              });
                          });
                      });
                  });
              })
            : // One qualification; move to subjects function
              filterSubjectData(entryPoints[0]['options']);
    }
